.txt-score {
    margin-top: 3rem;
    font-size: 70%;
}

.btn-wrap {
    display: flex;
    gap: 15px;
    width: 70%;
}
.btn-start {
    border: solid 1px #DAF5FF;
    color: #f8f8f8;
    font-weight: 600;
    padding: 15px;
    display: block;
    border-radius: 6px;
    cursor: pointer;
    background-color: #111;
    margin-top: 6rem;
    cursor: pointer;
}
.btn-start:hover {
    border-color: #28a124;
}


@media screen and (min-width: 800px) {
    .btn-wrap {
        width: 40%;
    } 
}
.txt-description {
    font-size: 60%;
    font-style: italic;
    margin-bottom: 3.5rem;
    color: #bbb;
}
.btn-wrap span {
    display: block;
    margin: auto 0;
    font-size: 1rem;
}
.quiz button {
    border: solid 1px #DAF5FF;
    color: #f8f8f8;
    font-weight: 600;
    padding: 15px;
    display: block;
    border-radius: 6px;
    cursor: pointer;
}
.question-options {
    display: flex;
    flex-flow: row wrap;
    gap: 40px;
    --active-color: #28a124;
    --btn-color: #0D8EB9;
    justify-content: center;
}
.txt-center {
    text-align: center;
}
.txt-topic {
    font-size: 90%;
}
.questions-set button {
    background-color: var(--btn-color);
    width: 100%;
 
}
.questions-set button:hover {
    background-color: var(--active-color);
}

.btn-reset {
    margin: 0 auto;
    background-color: #000;
    margin-top: 40px;   
}
.btn-reset:hover, .btn-reset:focus-within {
    border-color: #28a124;
}
.endgame {
    margin: 4rem auto 6rem auto;
}
.result  {
    font-size: 80%;
}